class LugarPago {
    constructor(index, lugar, lat, lng, map, slides) {
        this.index = index;
        this.lat = lat;
        this.lng = lng;
        this.marker = map.addMarkerSync({
            //title: lugar.Nombre,
            //snippet: lugar.Servicios,
            animation: "DROP",
            icon: {
                url: "./assets/icons/place.png",
                size: {
                    width: 25,
                    height: 25
                }
            },
            position: {
                lat: this.lat,
                lng: this.lng
            }
        });
        this.marker.on(_ionic_native_google_maps_ngx__WEBPACK_IMPORTED_MODULE_7__["GoogleMapsEvent"].MARKER_CLICK).subscribe(() => {
            this.clickMarker(slides);
        });
    }
    /**
     * Cuando se selecciona una marca
     * @param slides
     */
    clickMarker(slides) {
        //actualizar slide
        slides.slideTo(this.index, 200);
    }
    /**
     * Cuando se actualiza el slide
     * @param map
     */
    seleccionado(map, slides) {
        //centrar
        let position = this.marker.getPosition();
        map.animateCamera({
            target: {
                lat: position.lat,
                lng: position.lng
            },
            duration: 200
        });
        //icono
        this.marker.setIcon({
            url: './assets/icons/marker-selected.png',
            size: {
                width: 20,
                height: 35
            }
        });
        //animacion
        this.marker.setAnimation("DROP");
        //info
        //this.marker.showInfoWindow();
    }
    /**
     * Cuando se deselecciona una marca
     */
    noSeleccionado() {
        this.marker.setIcon({
            url: './assets/icons/place.png',
            size: {
                width: 25,
                height: 25
            }
        });
    }
    /**
     * Obtener indice de la marca
     */
    getIndex() {
        return this.index;
    }
}


